// Colors
// -----------------------------------------------------------------------------
$colors: (
  blue:         #3498db,
  blue-dark:    #196090,
  gray-dark:    #2c3e50,
  gray-light:   #ebeae4,
  brown:        #796b4f,
  purple:       #8e44ad,
  graphite:     #34495e,
  green:        #27ae60,
  orange:       #f44336,
