$brand-primary: color(orange);

// Form
// -----------------------------------------------------------------------------
$input-height:    35px;

// Shadow
// -----------------------------------------------------------------------------
$shadow: 0 1px 6px rgba(black, .12), 0 1px 4px rgba(black, .12);
